import * as React from "react";
import { Flex, Image, keyframes, IconButton } from "@chakra-ui/react";
import { ChevronRightIcon, ChevronDownIcon } from "@chakra-ui/icons";

export default function IndividualQuestion({ isActive, children }) {
	return (
		<Flex
			fontWeight={"bold"}
			bg={"rgba(365,365,365,0.5)"}
			padding={"5px"}
			justifyContent={"space-between"}
			cursor={"pointer"}
		>
			{children}
			<IconButton
				cursor={"pointer"}
				w={"25px"}
				h={"25px"}
				colorScheme={"white"}
				aria-label="Open question"
				icon={
					isActive ? (
						<ChevronRightIcon w={"30px"} h={"30px"} color={"black"} />
					) : (
						<ChevronDownIcon w={"30px"} h={"30px"} color={"black"} />
					)
				}
			/>
		</Flex>
	);
}
